#charity {
  position: relative;
  background-color: $secondaryColor;
  min-height: 2196px;

  color: $primaryColor;

  background-image: url(../images/charity/charity-bg.jpg);
  background-repeat: no-repeat;
  background-position: center;

  & .container-full {
    width: 100%;
    padding-top: 120px;
    padding-bottom: 120px;
    padding-top: 330px;
    padding-bottom: 250px;

    border-top-left-radius: 60px;
    border-top-right-radius: 60px;
    background-color: $secondaryColor;

    margin-top: -50px;
  }
}

.sun-element {
  position: absolute;
  top: 20px;
  left: 50%;

  transform: translateX(-50%);
}

.charity__header {
  text-align: center;
}
